  border-style: none;
  padding: 0;
}

/**
 * Restore the focus outline styles unset by the previous rule in Firefox.
 */

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

/**
 * Remove the additional :invalid styles in Firefox.
 */

:-moz-ui-invalid {
  box-shadow: none;
}

/* Interactive
 * ========================================================================== */

/*
 * Add the correct display in Edge 18- and IE.
 */

details {
  display: block;
}

/*
 * Add the correct styles in Edge 18-, IE, and Safari.
 */

dialog {
  background-color: white;
  border: solid;
  color: black;
  display: block;
  height: -moz-fit-content;
  height: -webkit-fit-content;
  height: fit-content;
  left: 0;
  margin: auto;
  padding: 1em;
  position: absolute;
  right: 0;
  width: -moz-fit-content;
  width: -webkit-fit-content;
  width: fit-content;
}

dialog:not([open]) {
  display: none;
}

/*
 * Add the correct display in all browsers.
 */

summary {
  display: list-item;
}

/* Scripting
 * ========================================================================== */

/**
 * Add the correct display in IE.
 */

template {
  display: none;
}

/* User interaction
 * ========================================================================== */

body {
	margin: 0;
}

html {
	font-size: 16px;
}

@media screen and (max-width: 700px) {
	html {
		font-size: 14px;
	}
}

@media screen and (max-width: 550px) {
	html {
		font-size: 12px;
	}
}

.Home{width:100%;height:calc(100vh - 2em);position:relative}.Home__circle{width:25em;height:25em;text-align:center;color:#fff;background:#000;border-radius:50%;border-width:1px;border-style:solid;border-image:linear-gradient(to bottom, #f20089, #2d00f7, #5a189a, #ff6d00) 1;display:flex;justify-content:center;align-items:center;position:absolute;top:calc(50% - 12.5em);left:calc(50% - 12.5em)}.Home__description{font-family:'Playfair Display', serif;font-size:1.6em;line-height:1.4em;font-style:italic}.Home__link>*{display:block;padding:0;margin:0;border:0;background:transparent;color:#fff;cursor:pointer;font-family:'Hammersmith One', serif;font-size:3em;line-height:1em;-webkit-text-decoration:0;text-decoration:0;text-transform:uppercase}.Home__link>*:hover{color:#DB00B6}.Home__link-list{display:flex;justify-content:center;position:absolute;bottom:0;width:100%;margin:0 0 1em;padding:0}.Home__link-list li{list-style:none;margin:0 .5em}.Home__link--left,.Home__link--right{position:absolute;width:20em;text-align:center;top:50%}.Home__link--left{transform:rotate(90deg);left:-7em}.Home__link--right{transform:rotate(-90deg);right:-7em}@media screen and (max-width: 550px){.Home__link--left,.Home__link--right{margin-top:1em;width:100%;transform:none;position:relative;top:auto;left:auto;right:auto}}

.Header{font-family:'Playfair Display', serif;font-size:4em;font-style:italic;letter-spacing:5px;margin:0 0 1em;padding:0}

.Paragraph{margin-bottom:5em}.Paragraph__secondary{opacity:.5;font-size:1.2em;margin:0 0 .5em}.Paragraph__header{font-size:2.4em;margin:0 0 .75em;font-family:'Playfair Display', serif;letter-spacing:3px}.Paragraph__main{font-size:1.5em;line-height:1.6em;word-spacing:2px;margin:0}

.Tags{display:flex;flex-wrap:wrap;justify-content:space-between}.Tags__item{font-family:"Playfair Display SC";font-size:2.4em;margin:.25em .5em}

.Tile{height:10em;background:#5a189a;padding:2em;display:flex;flex-direction:column}.Tile__text{margin:0 0 .75em;font-size:1.7em;font-family:'Playfair Display', serif;letter-spacing:3px}.Tile__subtext{opacity:.5;font-size:1.2em;margin-top:auto}

.Slider{font-family:'Hammersmith One', serif;text-transform:uppercase;text-align:center;margin:4rem 0}.Slider__main{font-size:10rem;color:#DB00B6;margin:0}.Slider__main::first-letter{color:#fff}.Slider__secondary{color:#fff;font-size:2rem;margin:0}

.Developer{width:60%;margin:auto;position:relative}.Developer__experience{display:flex;flex-wrap:wrap}.Developer__experience>.Tile{flex:1 1 auto;margin:1em 1em 0 0}.Developer__projects .Paragraph{margin-bottom:2em;width:70%}.Developer__link{font-size:1.5em;font-family:'Hammersmith One', serif;text-transform:uppercase;-webkit-text-decoration:0;text-decoration:0;color:#000;background:#fff;cursor:pointer;padding:.25em .5em;margin-right:.5em}.Developer__project{margin-bottom:5em}.Developer__section{margin-bottom:10em}.Developer__section.is-centered{text-align:center}.Developer__section-row{display:flex}

.Mentor{width:60%;margin:auto}.Mentor__section{margin-bottom:10em}

.Footer{position:absolute;left:0;margin:0;width:100%;padding:2em 0;text-align:center}.Footer__text{margin-top:2em}.Footer__link{font-family:'Hammersmith One', serif;text-transform:uppercase;text-decoration:none;color:#fff;font-size:1.25em;margin:0 .25em}.Footer__links{display:flex;justify-content:center}

.App{width:calc(100% - 2em);height:100%;background-color:#000;border-width:1em;border-style:solid;border-image:linear-gradient(to bottom, #f20089, #2d00f7) 1;color:#fff;font-family:'Alata', sans-serif}

